.nav__wrapper {
  width: 320px;
  margin: 0 auto;
  text-align: center;
}

.nav {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
}

.nav__link {
  text-decoration: none;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
}


.nav__list {
  list-style: none;
  color: rgb(255, 255, 255);
  background-color: rgba(129, 179, 210, 1);
  margin: 0;
  padding: 0;
}

.nav__list__item {
  padding: 17px 0 12px;
  filter: drop-shadow(0 1px 4px #01010133);
  box-shadow: 0 1px 0 0 rgba(255, 255, 255, 0.2) inset;
}

